.login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--main-bg-color);
    border-radius: 0.5rem;
    width: 32rem;
    height: 12rem;
    box-shadow: 0px 10px 20px -5px rgba(0, 0, 0, 0.5);
    pointer-events: all;
}